@import '../../assets/style/colors.scss';


table {
    border-collapse: collapse;
    line-height    : 1.1;
    font-family    : "Lucida Sans Unicode", "Lucida Grande", sans-serif;
    color          : #0C213B;
    margin         : 0 auto;
    min-width      : 700px;
}

th {
    border          : 2px solid $secondary-color;
    background-color: whitesmoke;
    height          : 2rem;
}

td {
    width     : 9rem;
    border    : 2px solid $secondary-color;
    text-align: center;
    height    : 2rem;
}

td:first-child {
    width           : 99px;
    color           : rgb(45, 68, 68);
    background-color: whitesmoke;
}
